@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&family=Roboto:wght@300&display=swap);
:root {
  --primary-black: #000000;
  --primary-white: #fff;
  --primary-brown: #a38d65;
  --primary-red: #c30d32;
  --primary-dark: #212529;
  --secondary-brown: #816126;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto Condensed', sans-serif !important;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 0.3rem;
  height: 0.3rem;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
  border-radius: 1rem;
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

#tsparticles{
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 1;
}
/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 35.99875rem) {
    .header__info h1 {
        font-size: 1.2rem;
    }

    .typed-text span{
        font-size: 1rem;
    }

    .header__btn {
        font-size: 0.8rem;
    }

    .slider__title {
        font-size: 1.5rem;
    }

    .slider__description {
        font-size: 0.6rem;
    }
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 36rem) and (max-width: 47.99875rem) {

    .header__info h1 {
        font-size: 1.5rem;
    }

    .typed-text span{
        font-size: 1rem;
    }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 48rem) and (max-width: 61.9rem) {
    
}

.logo {
    width: 3rem;
}

/* ***************   MENU LINK   ************** */
.navbar {
    position: fixed;
    z-index: 9;
    top: 0;
    left: 0;
    right: 0;
}

.navbar-nav {
    margin-left: auto;
}

nav .nav-link {
    position: relative;
    padding: 0 !important;
    font-weight: 600;
    letter-spacing: 0.0375rem;
    display: flex;
}

.nav-link::after {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 0.0625rem;
    width: 0;
    background-color: var(--primary-white);
    transition: 0.3s;
    content: "";
}

.nav-link.active::after {
    width: 100%;
}

.nav-item:hover .nav-link::after {
    width: 100%;
}

nav ul li {
    text-transform: uppercase;
}

.navbar-nav .nav-link {
    margin: 0.75rem;
    color: var(--primary-white) !important;
}

.nav-link p {
    margin: 0;
}

.nav-item {
    position: relative;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.nav-icon__container {
    display: flex;
}

.nav-icon {
    display: flex;
    color: var(--primary-white);
    margin-left: 0.5rem;
}

.nav-subnav {
    position: absolute;
    padding: 1rem 0.5rem 0.5rem 0.5rem;
    left: -30rem;
    right: 0;
    top: 100%;
    list-style: none;
    background: var(--primary-white);
    max-height: 20rem;
    overflow-y: scroll;
    display: none;
}

.nav-item:hover > .nav-subnav {
    display: block;
}

.fav__item {
    display: flex;
    justify-content: space-around;
    align-items: center;
    white-space: nowrap;
    color: var(--primary-black);
    margin-bottom: 1rem;
    cursor: pointer;
}

.fav__item:hover {
    background: #ccc;
}

.fav__index {
    max-width: 1rem;
}

.fav__null {
    margin: 1rem 2rem 0.5rem;
    text-align: center;
    text-transform: none;
    text-transform: initial;
}

.fav__img {
    height: 3rem;
    object-fit: contain;
}

.fav__count {
    position: absolute;
    font-weight: bold;
    top: 0;
    right: 0rem;
    background-color: var(--primary-brown);
    color: var(--primary-white);
    display: flex;
    text-align: center;
    justify-content: center;
    width: 1.3rem;
    height: 1.3rem;
    border-radius: 50%;
}

.fav-table__head th,
.fav-table__body {
    text-align: center;
}

.fav-table__body td {
    text-transform: none;
    text-transform: initial;
    line-height: 3rem;
    max-width: 7rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

@media (min-width: 35.5rem) and (max-width: 50.75rem) {
    .navbar-nav > li > a {
        font-size: 0.8rem;
        margin: 0.5rem;
    }
}

@media (max-width: 61.938rem) {
    ::-webkit-scrollbar {
        display: none;
    }
    .navbar {
        max-height: 90vh;
        overflow-y: scroll;
    }
    .hide-on-mobile {
        display: none;
    }
}
.header {
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

@media (max-width: 47.99875rem) {
    .header {
        top: 4.6rem;
    }
}

.header__container {
    position: relative;
    background: url(/sumostore/static/media/header-bg-img.f6f537b3.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
}

.header__info {
    display: flex;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    z-index: 2;
}

.header__info h1 {
    text-transform: uppercase;
    font-weight: 600;
    color: var(--primary-brown);
}

.typed-text {
    text-shadow: 0 0 0.25rem var(--primary-black);
    color: var(--primary-white);
    font-size: 2rem;
}

.header__btn {
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
    border: 1px solid var(--primary-white);
    background-color: var(--primary-brown);
    padding: 0.625rem;
    margin: 2rem;
    color: var(--primary-white);
    transition: 0.2s;
}

.header__btn:hover {
    background-color: var(--primary-white);
    color: var(--primary-black);
}


.slider__img {
    height: 100vh;
    object-fit: cover;
}

.slider__title,
.slider__description {
    text-shadow: 0 0 0.25rem var(--primary-black);
}

.slider__description {
    word-spacing: 0.1rem;
    font-size: 1rem;
}

.slider__link {
    text-decoration: none;
    border: 0.0625rem solid var(--primary-brown);
    color: var(--primary-white);
    background-color: var(--primary-black);
    padding: 0.0625rem 0.125rem;
}

.slider__link:hover {
    background-color: var(--primary-brown);
    color: var(--primary-black);
    text-shadow: none;
    text-shadow: initial;
    font-weight: 600;
}
.login {
    position: relative;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(/sumostore/static/media/image1.8603d421.jpg) no-repeat;
    background-position: center;
    background-size: cover;
}

@media (min-width: 35.5rem) and (max-width: 51rem) {
    .login {
        margin-top: 4.6rem;
    }
}

.login::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .65 );
}

.login__container {
    z-index: 1;
}

.login__form {
    width: 21rem;
}

@media (max-width: 35.99875rem) {
    .login__form {
        width: 19rem;
    }
}

.login__title {
    font-weight: 600;
    font-size: 28px;
    color: #fff;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
}

.wrap-login__form {
    background-color: var(--primary-white);
    /* height: 20rem; */
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.login__form .form-group {
    position: relative;
    margin-top: 1rem;
    border-bottom: 0.063rem solid var(--primary-brown);
    padding: 1rem 0.5rem;
    width: 90%;
}

.login__icon {
    position: absolute;
    left: 0;
    bottom: 1.4rem;
    color: #edce96;
}

.login__input {
    border: none;
    outline: none;
    width: 100%;
    font-size: 1rem;
    padding-left: 1rem;
}

.form__btn {
    /* font-family: Ubuntu-Bold; */
    text-transform: uppercase;
    font-weight: 600;
    margin: 2rem 0;
    border: none;
    width: 6.25rem;
    height: 3.125rem;
    border-radius: 1rem;
    background: #edce96;
    transition: .3s;
}
.form__btn:hover {
    background: #333;
    color: var(--primary-white);
}

.form__subtitle {
    margin-top: 2rem;
    color: var(--primary-white);
}

.forgot-password{
    margin: 1rem 0 0 1rem;
    align-self: flex-start;
}
.register {
    position: relative;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(/sumostore/static/media/image1.8603d421.jpg) no-repeat;
    background-position: center;
    background-size: cover;
}

@media (min-width: 35.5rem) and (max-width: 51rem) {
    .register {
        margin-top: 4.6rem;
    }
}

.register::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .65 );
}

.register__container {
    z-index: 1;
    width: 21rem;
    margin-top: 5rem;
}

@media (max-width: 35.99875rem) {
    .register__container {
        width: 19rem;
    }
}

.register__form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.register__title {
    font-weight: 600;
    font-size: 28px;
    color: #fff;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
}

.form__label {
    color: #fff;
}

.register__input {
    border: none;
    outline: none;
    width: 100%;
    font-size: 1rem;
    padding: 0 1rem;
    height: 2rem;
    border-radius: 1rem;
}

.form__btn.btn--register {
    margin: 0;
}
.add-vehicle {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
}

@media (min-width: 35.5rem) and (max-width: 51rem) {
    .add-vehicle {
        margin-top: 4.6rem;
    }
}

.add-vehicle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url(/sumostore/static/media/trade.a63b61d8.jpg);
    background-size: cover;
    background-position: center;
    opacity: 0.7;
    z-index: -1;
}

.add__container {
    position: relative;
    margin: 6rem 0 2rem;
    background-color: rgba(163, 141, 101, 0.4);
    max-width: 41.25rem;
    border: 0.0625rem solid var(--primary-brown);
    border-radius: 3%;
    padding: 1rem;
}

.add__label {
    font-weight: 600;
    text-shadow: 0 0 0.0625rem var(--primary-white);
}

.add__btn {
    width: 96%;
    margin-left: 2%;
    border: 0.0625rem solid var(--secondary-brown);
    color: var(--primary-black);
    background-color: var(--primary-brown);
    box-shadow: none!important;
}

.add__btn:hover {
    background-color: var(--secondary-brown);
    border: 0.0625rem solid var(--primary-brown);
}
.add__btn:active,
.add__btn:focus {
    color: var(--primary-black);
    border: 0.0625rem solid var(--primary-brown);
    background-color: var(--primary-brown);
}
.more__btn {
    width: 16rem;
    height: 3rem;
    margin-top: 1.563rem;
    margin-bottom: 1.563rem;
    font-weight: 600;
    border-radius: 2rem;
    border: none;
    background: var(--primary-brown);
    -webkit-appearance: none;
}

.more__btn:hover {
    background: var(--secondary-brown);
    color: var(--primary-white);
    box-shadow: 0 0 0.125rem var(--primary-white);
}

.more__btn--load:hover {
    cursor: unset;
    background: var(--primary-brown);
    box-shadow: none;
    color: #000;
    color: initial;
}
.vehicle {
    position: relative;
    margin-top: 4.6rem;
    min-height: 100vh;
    padding-bottom: 1rem;
}

.vehicle__container {
    margin-top: 1.5rem;
}

.vehicle__element {
    height: 100%;
    border: 1px solid var(--primary-brown);
    background-color: var(--primary-brown);
}

.vehicle__img-link {
    padding: 1rem;
}

.vehicle__img-container {
    position: relative;
    height: 100%;
    perspective: 8rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.vehicle__img {
    height: 16rem;
    width: 100%;
    object-fit: cover;
    border-radius: 2%;
    transition: 0.3s;
    cursor: pointer;
    background-color: var(--primary-white);
}

.vehicle__img-container:hover .vehicle__img {
    transform: translateZ(0.5rem);
    opacity: 0.8;
}

.detail__btn {
    position: absolute;
    color: var(--primary-white);
    text-shadow: 0 0 0.1rem var(--primary-black);
    background-color: rgba(163, 141, 101, 0.6);
    width: 60%;
    height: 12%;
    border-radius: 1rem;
    font-weight: 600;
    transition: .4s;
    border: none;
    opacity: 0;
}

.vehicle__img-container:hover .detail__btn {
    opacity: 1;
    animation: detail .5s linear;
}

@keyframes detail {
    0% {
        transform: translateX(-2rem);
    }
    50% {
        transform: translateX(2rem);
    }
    100% {
        transform: translateX(0);
    }
}

.card__body {
    border-top: 0.063rem solid var(--primary-black) ;
    padding: 0.5rem 0 0;
    margin: 0 1rem 1rem;
}

.vehicle__title,
.vehicle__subtitle {
    font-weight: 600;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.vehicle__subtitle {
    margin-bottom: 0.3rem;
}

.sold-out,
.spinner {
    position: absolute;
    left: 0;
    top: 2rem;
    height: 80%;
    width: 100%;
    color: var(--primary-brown);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal__description {
    display: flex;
    justify-content: space-between;
    font-weight: 600;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: pre-wrap;
}

.modal__description button {
    width: 3rem;
    height: 1.6rem;
    /* border-radius: 1rem; */
    border: none;
    background: var(--primary-brown);
    font-weight: bold;
}
.modal__description button:hover {
    background: var(--secondary-brown)
}

.modal__name {
    font-weight: 600;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.filter__title{
    white-space: nowrap;
    line-height: 2rem;
    margin: 0 1rem 0 0;
}

.filter__box {
    height: 2rem;
    font-size: 0.9rem;
    border-radius: 1rem;
    border-color: inherit;
    box-shadow: none;
}

.filter__box:focus {
    box-shadow: none;
    border-color: inherit;
    -webkit-box-shadow: none;
}

.fav-icon__container {
    display: flex;
    justify-content: flex-end;
}

.fav-icon {
    cursor: pointer;
    color: var(--primary-white);
}

.fav-icon.active {
    color: var(--primary-red);
}
.terms {
    margin-top: 4.6rem;
}
.sale-table__head th,
.sale-table__body tr {
    text-align: center;
}

.sale-table__head tr th {
    min-width: 6.5rem;
}

.sale-table__body tr td{
    max-width: 6.5rem;
    line-height: 4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.detail-btn {
    cursor: pointer;
}

.detail-btn:hover {
    background-color: #ccc;
}
